<script>
import Header from "../../components/global/header/HeaderArea2.vue"
import CitySelecter from "../../components/global/CitySelecter.vue"
import { ref, onMounted, watch } from 'vue'
import { apiGetCourseInfo, apiPostCourseApply } from "@/api/api"
import { useRouter } from "vue-router"
import { getUrlId,errorHandle } from "@/utils/handle"
import { Toast } from '../../components/global/swal.js'


export default {
    setup() {
        const router = useRouter()
        let courseData = ref([])
        let urlId = getUrlId().urlId
        let classList = ref()
        let postData = ref({
            "courseId": urlId,
            "courseClassId": null,
            "name": null,
            "gender": "Male",
            "birthday": null,
            "phoneNumber": null,
            "telephone": null,
            "mail": null,
            "cityId": null,
            "areaId": null,
            "address": null,
            "connect": {
                "name": null,
                "relationship": null,
                "phoneNumber": null,
                "telephone": null
            }
        })
        onMounted(async () => {
            courseData.value = await apiGetCourseInfo({ id: urlId })
            courseData.value = courseData.value.data
            classList.value = courseData.value.courseClass
            // console.log(classList.value)
        })

        const splitDate = (val) => {
            let array = val.split(" ")
            return array
        }

        const updateCityIdx = (val) => {
            postData.value.cityId = val.value
        }
        const updateAreaIdx = (val) => {
            postData.value.areaId = val.value
        }

        const form = ref(null)
        const save = async (e) => {
            e.preventDefault()
            if (form.value.reportValidity()) {
                if (!postData.value.courseClassId) {
                    Toast("請選擇報名班別")
                }
                if (!postData.value.cityId || !postData.value.areaId || !postData.value.address) {
                    Toast("請輸入正確地址")
                }
                let msg = await apiPostCourseApply(postData.value)
                if (msg.status == true) {
                    Toast("報名成功")
                    router.push({ path: `/course/info/${urlId}` })
                } else {
                    let errorMsg = errorHandle(msg)
                    Toast(errorMsg)
                }
                // console.log(postData.value)
            }
        }

        return { urlId, postData, classList, updateCityIdx, updateAreaIdx, save, form };
    },
    components: {
        Header, CitySelecter
    }
};
</script>

<template>
    <Header title="課程內容" path="-1" />
    <div class="newCourse">
        <div class="title">
            <p>我要報名</p>
        </div>
        <hr>
        <form ref="form">
            <div class="main1">
                <div class="mb-3">
                    <label class="form-label">請選擇班別<span class="star">*</span></label>
                    <div class="input-list">
                        <div class="form-check-inline" v-for="item in classList" :key="item.id">
                            <input type="radio" class="form-check-input" :value="item.id" :id="item.id"
                                v-model="postData.courseClassId" required>
                            <label class="form-check-label" :for="item.id">{{ item.name }}</label>
                        </div>
                    </div>
                </div>
                <div class="mb-3">
                    <label class="form-label">姓名<span class="star">*</span></label>
                    <input type="text" class="form-control" placeholder="請輸入姓名" v-model="postData.name" required>
                </div>
                <div class="mb-3">
                    <label class="form-label">性別<span class="star">*</span></label>
                    <div class="input-group">
                        <div class="col form-check">
                            <input type="radio" class="form-check-input" id="Male" value="Male"
                                v-model="postData.gender">
                            <label class="form-check-label" for="Male">男性</label>
                        </div>
                        <div class="col form-check">
                            <input type="radio" class="form-check-input" id="Female" value="Female"
                                v-model="postData.gender">
                            <label class="form-check-label" for="Female">女性</label>
                        </div>
                    </div>
                </div>
                <div class="mb-3">
                    <label class="form-label">出生日期<span class="star">*</span></label>
                    <input type="date" class="form-control" v-model="postData.birthday" required>
                </div>
                <div class="mb-3">
                    <label class="form-label">電話<span class="star">*</span></label>
                    <input type="text" class="form-control" placeholder="請輸入電話"
                        v-model="postData.phoneNumber" required>
                </div>
                <div class="mb-3">
                    <label class="form-label">手機號碼<span class="star">*</span></label>
                    <input type="text" class="form-control" placeholder="請輸入手機號碼" v-model="postData.telephone"
                        pattern="^09\d{2}?\d{3}?\d{3}$" required>
                </div>
                <div class="mb-3">
                    <label class="form-label">電子信箱<span class="star">*</span></label>
                    <input type="email" class="form-control" placeholder="請輸入電子信箱" v-model="postData.mail" required>
                </div>
                <div class="mb-3">
                    <label class="form-label">通訊地址<span class="star">*</span></label>
                    <CitySelecter @city-idx="updateCityIdx" @area-idx="updateAreaIdx" />
                    <input type="text" class="form-control mt-3" placeholder="請輸入其他地址" v-model="postData.address">
                </div>
                <div class="mb-3">
                    <label class="form-label">緊急聯絡人<span class="star">*</span></label>
                    <input type="text" class="form-control" placeholder="請輸入緊急聯絡人" v-model="postData.connect.name"
                        required>
                </div>
                <div class="mb-3">
                    <label class="form-label">緊急聯絡人關係<span class="star">*</span></label>
                    <input type="text" class="form-control" placeholder="請輸入緊急聯絡人關係"
                        v-model="postData.connect.relationship" required>
                </div>
                <div class="mb-3">
                    <label class="form-label">緊急聯絡人電話<span class="star">*</span></label>
                    <input type="text" class="form-control" placeholder="請輸入緊急聯絡人電話"
                        v-model="postData.connect.phoneNumber" required>
                </div>
                <div class="mb-3">
                    <label class="form-label">緊急聯絡人手機號碼<span class="star">*</span></label>
                    <input type="text" class="form-control" placeholder="請輸入緊急聯絡人手機號碼"
                        v-model="postData.connect.telephone" pattern="^09\d{2}?\d{3}?\d{3}$" required>
                </div>
                <div class="mt-4b btn-container">
                    <div class="row">
                        <div class="col">
                            <button class="btn btn-outline-primary" type="button" @click="save">完成報名</button>
                        </div>
                    </div>
                </div>
            </div>
        </form>
    </div>

</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.newCourse {
    margin-top: $header-height;
    margin-bottom: calc($footer-height + 15px);
    padding: 1.25rem;
}

Header {
    background-color: #FFFFFF;
}

.title {
    font-size: 1.25rem;
    font-weight: 700;
    display: flex;
    justify-content: center;
}
</style>